@use 'src/template.scss' as *;

li {
    border: none;
    border-bottom: white solid 1px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    width: 100%;
    box-sizing: border-box;

    p {
        margin: 0
    }

    .track {
        @include flex-vertical;
        align-items: flex-start;
        
        max-width: 80%;
    }
    
    h3 {
        margin: 0;
    }

    .circle {
        border-radius: 50px;
        width:40px;
        height: 40px;
        @include flex-vertical;
        justify-content: center;

        &:hover {
            cursor: pointer;
        }

        &:active {
            border: 1px solid white;
        }

    }
}

