@use 'src/template.scss' as *;

@mixin bg-color {
    background-color: rgba(0, 0, 0, 0.606)
}

.playlist-container {
    @include bg-color;
    padding-block: 20px;
    width: 45%;
    input[type=text] {
        color: white;
        background-color: rgba(0, 0, 0, 0);
        border: none;
        border-bottom: 2px solid white;
        border-radius: 0;
        width: 80%;
        margin-bottom: 30px;
        font-size: 20px;
    }
}

.playlist > ul {
    list-style: none;
    text-align: left;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    margin: 10px 10px 20px 10px;
    overflow-y: scroll;
    max-height:500px;

    p {
    color: lightgray;
    }
}